@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.theme-switch {
  display: flex;
  align-items: center;
  font-size: 0.8em;
  padding: 0;
  background-color: transparent;
  border: 0;
}

.theme-switch ion-icon {
  margin-right: 8px;
}

@media (min-width: 769px) {
  .theme-switch {
    font-size: 1em;
  }
}

.name-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 40px;
  border-radius: 5px;
}

.name-input-wrapper ion-icon {
  margin-left: 27px;
}

@media (min-width: 769px) {
  .name-input-wrapper ion-icon {
    font-size: 1.1em;
    margin-left: 33px;
  }
}

.name-input-wrapper__text-input {
  position: absolute;
  width: calc(100% - 110px);
  height: 100%;
}

@media (min-width: 426px) {
  .name-input-wrapper {
    flex: 2 1;
    min-width: 240px;
    max-width: 385px;
    margin-right: 20px;
  }
}

@media (min-width: 769px) {
  .name-input-wrapper {
    max-width: 480px;
    margin-bottom: 33px;
  }
}

.region-select {
  max-width: 192px;
  margin-bottom: 15px;
}

@media (min-width: 426px) {
  .region-select {
    flex: 1 1;
  }
}

@media (min-width: 769px) {
  .region-select {
    max-width: 200px;
  }
}

.country {
  display: -ms-grid;
  display: grid;
  margin: 0 auto;
  border-radius: 5px;
  -ms-grid-rows: minmax(auto, 160px) 1fr;
      grid-template-rows: minmax(auto, 160px) 1fr;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
}

.country > * {
  -ms-grid-column: 1;
  grid-column: 1;
}

.country__flag-wrapper {
  overflow: hidden;
  text-align: center;
}

.country__flag {
  width: 100%;
  height: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  object-fit: cover;
  -ms-grid-row: 1;
  grid-row: 1;
}

.country__details {
  padding: 10px 24px 40px;
  -ms-grid-row: 2;
  grid-row: 2;
}

.country h2 {
  margin: 16px 0;
}

.country p {
  font-size: 0.85em;
  font-weight: 600;
  margin: 4px 0;
}

.borders {
  -ms-grid-row: 5;
  grid-row: 5;
  -ms-grid-column: 1;
  grid-column: 1;
}

.borders__label {
  font-size: 1em;
  margin: 17px 0;
}

.borders__ul {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}

.borders__ul li {
  margin: 0 10px 10px 0;
  margin-right: 10px;
  margin-bottom: 10px;
}

.borders__ul .button-link {
  font-size: 0.8em;
}

@media (min-width: 769px) {
  .borders {
    margin-top: 40px;
    -ms-grid-row: 4;
    grid-row: 4;
    grid-column: 1 / -1;
  }
  .borders__ul li {
    margin-right: 10px;
  }
}

@media (min-width: 1200px) {
  .borders {
    align-items: flex-start;
    -ms-grid-row: 4;
    grid-row: 4;
    -ms-grid-column: 2;
    grid-column: 2;
  }
  .borders__label {
    min-width: 130px;
  }
}

@media (min-width: 1441px) {
  .borders {
    display: flex;
  }
  .borders__label {
    margin: 0 10px 0 0;
  }
}

.country-details {
  display: -ms-grid;
  display: grid;
  max-width: 1500px;
  margin: 0 auto;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
  -ms-grid-rows: auto auto auto auto auto;
      grid-template-rows: auto auto auto auto auto;
}

.country-details strong {
  font-weight: 600;
}

.country-details > .button-link {
  -ms-grid-row: 1;
  grid-row: 1;
  -ms-grid-column: 1;
  grid-column: 1;
}

.country-details__flag {
  width: 100%;
  max-width: 450px;
  height: auto;
  margin: 64px auto 41px;
  -ms-grid-row: 2;
  grid-row: 2;
  -ms-grid-column: 1;
  grid-column: 1;
}

.country-details__header {
  font-size: 1.3em;
  margin: 4px 0 0;
  -ms-grid-column: 1;
  grid-column: 1;
  -ms-grid-row: 3;
  grid-row: 3;
}

.country-details__text {
  font-size: 0.9em;
  display: flex;
  flex-direction: column;
  margin: 10px 0;
  -ms-grid-row: 4;
  grid-row: 4;
  -ms-grid-column: 1;
  grid-column: 1;
}

.country-details__text p {
  margin: 12px 0;
}

.country-details__text div:first-child {
  margin-bottom: 22px;
}

@media (min-width: 426px) {
  .country-details {
    padding: 50px 8vw 40px;
  }
}

@media (min-width: 580px) {
  .country-details__flag {
    margin: 64px auto 50px;
  }
  .country-details__text {
    flex-direction: row;
    justify-content: space-between;
  }
  .country-details__text div:first-child {
    margin-right: 40px;
    margin-bottom: 0;
  }
}

@media (min-width: 769px) {
  .country-details {
    -ms-grid-columns: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
    -ms-grid-rows: 120px 91px auto auto;
        grid-template-rows: 120px 91px auto auto;
    -webkit-column-gap: 70px;
            grid-column-gap: 70px;
            column-gap: 70px;
  }
  .country-details > .button-link {
    -ms-grid-row: 1;
    grid-row: 1;
    -ms-grid-column: 1;
    grid-column: 1;
  }
  .country-details__flag {
    max-width: unset;
    margin: 0;
    grid-row: 2 / -1;
    -ms-grid-column: 1;
    grid-column: 1;
  }
  .country-details__header {
    font-size: 2em;
    margin-top: 40px;
    -ms-grid-row: 2;
    grid-row: 2;
    -ms-grid-column: 2;
    grid-column: 2;
  }
  .country-details__text {
    flex-direction: column;
    -ms-grid-row: 3;
    grid-row: 3;
    -ms-grid-column: 2;
    grid-column: 2;
  }
  .country-details__text div:first-child {
    margin-bottom: 20px;
  }
  .country-details__text p {
    font-size: 1.15em;
    margin: 9px 0;
  }
}

@media (min-width: 1025px) {
  .country-details {
    -webkit-column-gap: 100px;
            grid-column-gap: 100px;
            column-gap: 100px;
    -ms-grid-columns: minmax(auto, 500px) 1fr;
        grid-template-columns: minmax(auto, 500px) 1fr;
  }
}

@media (min-width: 1200px) {
  .country-details {
    -ms-grid-columns: minmax(auto, 560px) 1fr;
        grid-template-columns: minmax(auto, 560px) 1fr;
    -webkit-column-gap: 120px;
            grid-column-gap: 120px;
            column-gap: 120px;
  }
  .country-details__text {
    flex-direction: row;
  }
  .country-details__text div:first-child {
    margin-bottom: 0;
  }
}

@media (min-width: 1441px) {
  .country-details__text {
    justify-content: flex-start;
  }
}

header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 30px 16px;
}

@media (min-width: 426px) {
  header {
    padding: 35px 11vw;
  }
}

@media (min-width: 769px) {
  header {
    padding: 24px 5.6vw;
  }
}

@media (min-width: 1441px) {
  header {
    padding-right: 12vw;
    padding-left: 12vw;
  }
}

body {
  font-family: "Nunito Sans", sans-serif;
  margin: 0;
}

h1 {
  font-size: 0.9em;
  font-weight: 800;
  margin: 0;
}

@media (min-width: 426px) {
  h1 {
    font-size: 1.2em;
  }
}

@media (min-width: 769px) {
  h1 {
    font-size: 1.5em;
  }
}

h2 {
  font-size: 1.1em;
}

h2 a {
  text-decoration: none;
}

.countries-section {
  padding: 25px 20px 30px;
}

@media (min-width: 426px) {
  .countries-section {
    padding: 30px 11vw 35px;
  }
}

@media (min-width: 769px) {
  .countries-section {
    padding: 48px 5.5vw 45px;
  }
}

@media (min-width: 1441px) {
  .countries-section {
    padding-right: 12vw;
    padding-left: 12vw;
  }
}

.country-list {
  display: -ms-grid;
  display: grid;
  padding: 0 34px;
  list-style-type: none;
  -ms-grid-columns: (minmax(250px, 1fr))[auto-fill];
      grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 40px;
  gap: 40px;
}

@media (min-width: 426px) {
  .country-list {
    padding: 0;
    grid-gap: 55px;
    gap: 55px;
  }
}

@media (min-width: 769px) {
  .country-list {
    grid-gap: 75px;
    gap: 75px;
  }
}

.country-details-section {
  padding: 40px 28px 30px;
}

@media (min-width: 426px) {
  .country-details-section {
    padding: 50px 11vw 45px;
  }
}

@media (min-width: 769px) {
  .country-details-section {
    padding: 80px 5.5vw 60px;
  }
}

.element {
  box-shadow: 0 0 11px -8px #111517;
}

.filter-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (min-width: 426px) {
  .filter-form {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.text-field {
  font-family: "Nunito Sans", sans-serif;
  font-size: 0.75em;
  padding: 0 40px 0 70px;
  border: 0;
  background-color: transparent;
}

@media (min-width: 769px) {
  .text-field {
    font-size: 0.9em;
    padding-left: 72px;
  }
}

.select {
  font-family: "Nunito Sans", sans-serif;
  font-size: 0.75em;
  font-weight: 600;
  box-sizing: border-box;
  padding: 0 16px;
  border: 0;
  border-radius: 5px;
}

@media (min-width: 769px) {
  .select {
    font-size: 0.9em;
    padding: 0 19px;
  }
}

button {
  font-family: "Nunito Sans", sans-serif;
}

button:hover {
  cursor: pointer;
}

.button-link {
  font-size: 0.9em;
  display: inline-block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 6px 23px;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
}

.button-link--icon {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.button-link--icon ion-icon {
  margin-right: 12px;
}

@media (min-width: 769px) {
  .button-link {
    padding: 6px 30px;
  }
  .button-link--icon {
    padding: 10px 36px;
  }
  .button-link--icon ion-icon {
    margin-right: 17px;
  }
}

.form-element {
  height: 46px;
}

@media (min-width: 769px) {
  .form-element {
    height: 55px;
  }
}

